
import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import SelectTab from "components/SelectTab/selectTab";
export default{
components: {
XHeader,Toast,SelectTab
},
data(){
return {
noData: false,
familyList: [],
}
},
mounted() {
this.qryMyFamilyList();
},
methods: {

qryMyFamilyList() {
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post('',{
"MSH.1": "wdjt",
"MSH.2": "qryMyFamilyList",
'GRDAID':this.$root.loginInfo.grdaid,
"CHZBM": this.$root.loginInfo.CHZBM,
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res);
this.$vux.loading.hide();
if(typeof res != 'undefined') {
this.familyList = [];
if (res.length > 0) {
this.noData = false;
this.familyList = res;
} else {
this.noData = true;
}
}
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
})
},

gotoGRDA(obj) {
this.$router.push({name:'healthRrecords'});
window.localStorage.setItem('other',JSON.stringify(obj));
},
CancelRelated() {
let THIS = this;
this.$vux.confirm.show({
title:'提示',
content:'取消关联档案后,将不能查看档案信息,确认取消关联档案?',

onCancel() {
},
onConfirm() {

THIS.$router.push({name: "FileLogin", params: {TYPE: "取消关联"}});
},
})
}
}
}



